/* HomePage.css - Premium Redesign */

/* Variables */
/* Variables */
:root {
    --primary-dark: #0f172a;
    /* Slate 900 - Graphite/Dark */
    --primary-green: #38bdf8;
    /* Sky 400 - Steel Blue-ish */
    --primary-green-hover: #0ea5e9;
    /* Sky 500 */
    --text-light: #f8fafc;
    --text-dark: #1e293b;
    /* Slate 800 */
    --text-gray: #64748b;
    /* Slate 500 */
    --bg-light: #ffffff;
    --bg-off-white: #f1f5f9;
    /* Slate 100 */
    --shadow-soft: 0 10px 30px rgba(15, 23, 42, 0.08);
    --shadow-hover: 0 20px 40px rgba(15, 23, 42, 0.12);
    --gradient-hero: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
    --font-primary: 'Inter', sans-serif;
    --font-secondary: 'Roboto', sans-serif;
}

.homepage {
    font-family: var(--font-primary);
    color: var(--text-dark);
    overflow-x: hidden;
    background-color: var(--bg-light);
}

.container {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 24px;
}

/* --- Top Navigation --- */
.top-nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(15px);
    padding: 1rem 0;
    z-index: 1000;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.04);
    transition: all 0.3s ease;
}

.top-nav ul {
    display: flex;
    justify-content: center;
    gap: 3rem;
    list-style: none;
    margin: 0;
    padding: 0;
}

.top-nav a {
    text-decoration: none;
    color: var(--primary-dark);
    font-weight: 500;
    font-size: 0.95rem;
    font-family: var(--font-secondary);
    transition: color 0.3s ease;
    position: relative;
}

.top-nav a::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: var(--primary-green);
    transition: width 0.3s ease;
}

.top-nav a:hover {
    color: var(--primary-green-hover);
}

.top-nav a:hover::after {
    width: 100%;
}

/* --- Hero Section --- */
.hero-section {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    background: var(--gradient-hero);
    color: var(--text-light);
    padding-top: 80px;
    /* Space for fixed nav */
    overflow: hidden;
}

/* Abstract Background Shapes */
.hero-section::before {
    content: '';
    position: absolute;
    top: -10%;
    right: -5%;
    width: 600px;
    height: 600px;
    background: radial-gradient(circle, rgba(0, 237, 100, 0.15), transparent 70%);
    border-radius: 50%;
    filter: blur(60px);
    animation: float 10s infinite ease-in-out;
}

.hero-section::after {
    content: '';
    position: absolute;
    bottom: -10%;
    left: -10%;
    width: 500px;
    height: 500px;
    background: radial-gradient(circle, rgba(0, 237, 100, 0.1), transparent 70%);
    border-radius: 50%;
    filter: blur(50px);
    animation: float 15s infinite ease-in-out reverse;
}

@keyframes float {

    0%,
    100% {
        transform: translate(0, 0);
    }

    50% {
        transform: translate(20px, -20px);
    }
}

.hero-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: center;
    position: relative;
    z-index: 2;
}

.hero-text {
    max-width: 600px;
}

.hero-title {
    font-family: var(--font-secondary);
    font-size: 4.5rem;
    font-weight: 700;
    line-height: 1.1;
    margin-bottom: 1.5rem;
    color: var(--text-light);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    background: linear-gradient(to right, #ffffff, #e0e0e0);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero-subtitle {
    font-size: 1.25rem;
    color: rgba(255, 255, 255, 0.9);
    margin-bottom: 2.5rem;
    line-height: 1.6;
    font-weight: 300;
}

.hero-actions {
    display: flex;
    gap: 1rem;
}

.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 2rem;
    border-radius: 50px;
    font-weight: 600;
    text-decoration: none;
    transition: transform 0.3s ease;
    cursor: pointer;
    background: linear-gradient(135deg, var(--primary-green), var(--primary-green-hover));
    box-shadow: var(--shadow-soft);
}

.btn:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-hover);
}

.btn-primary {
    background-color: var(--primary-green);
    color: var(--primary-dark);
    box-shadow: 0 4px 15px rgba(0, 237, 100, 0.3);
}

.btn-primary:hover {
    background-color: var(--primary-green-hover);
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(0, 237, 100, 0.4);
}

.btn-secondary {
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
    border: 1px solid rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
}

.btn-secondary:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: translateY(-2px);
}

.hero-image {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-image img {
    width: 100%;
    max-width: 500px;
    height: auto;
    filter: drop-shadow(0 20px 40px rgba(0, 0, 0, 0.3));
    animation: heroImageFloat 6s ease-in-out infinite;
}

@keyframes heroImageFloat {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-15px);
    }
}

/* --- Info Strip --- */
.info-strip {
    background: var(--primary-green);
    color: var(--primary-dark);
    padding: 1.5rem 0;
    font-weight: 600;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
}

.info-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.btn-outline {
    border: 2px solid var(--primary-dark);
    padding: 0.5rem 1.5rem;
    border-radius: 50px;
    color: var(--primary-dark);
    text-decoration: none;
    font-size: 0.9rem;
    transition: all 0.3s ease;
}

.btn-outline:hover {
    background: var(--primary-dark);
    color: var(--primary-green);
}

/* --- Quick Nav Section --- */
.quick-nav-section {
    padding: 6rem 0;
    background-color: var(--bg-off-white);
}

.section-title {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--primary-dark);
    margin-bottom: 3rem;
}

.quick-nav-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
}

.nav-card {
    background: white;
    padding: 2.5rem;
    border-radius: 20px;
    text-decoration: none;
    color: var(--text-dark);
    box-shadow: var(--shadow-soft);
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border: 1px solid transparent;
}

.nav-card:hover {
    transform: translateY(-10px);
    box-shadow: var(--shadow-hover);
    border-color: rgba(0, 237, 100, 0.3);
}

.nav-icon {
    width: 70px;
    height: 70px;
    background: rgba(0, 237, 100, 0.1);
    color: var(--primary-dark);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
    transition: all 0.3s ease;
}

.nav-card:hover .nav-icon {
    background: var(--primary-green);
    color: var(--primary-dark);
    transform: scale(1.1);
}

.nav-card h3 {
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
    font-weight: 600;
}

.nav-card p {
    font-size: 0.95rem;
    color: var(--text-gray);
    line-height: 1.5;
}

.nav-card.login-card {
    background: var(--primary-dark);
    color: white;
}

.nav-card.login-card .nav-icon {
    background: rgba(255, 255, 255, 0.1);
    color: var(--primary-green);
}

.nav-card.login-card h3 {
    color: white;
}

.nav-card.login-card p {
    color: rgba(255, 255, 255, 0.7);
}

.nav-cta i {
    margin-left: 5px;
    transition: transform 0.3s ease;
}

.nav-card:hover .nav-cta i {
    transform: translateX(5px);
}

/* --- Features Section --- */
.features-section {
    padding: 6rem 0;
    background: white;
}

.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 3rem;
}

.feature-item {
    text-align: left;
    padding: 2rem;
    border-radius: 20px;
    background: var(--bg-off-white);
    transition: transform 0.3s ease;
}

.feature-item:hover {
    transform: translateY(-5px);
}

.feature-icon {
    width: 60px;
    height: 60px;
    background: var(--primary-dark);
    color: var(--primary-green);
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
}

.feature-item h3 {
    font-size: 1.25rem;
    margin-bottom: 1rem;
    color: var(--primary-dark);
}

.feature-item p {
    color: var(--text-gray);
    font-size: 0.95rem;
}

/* --- News Section --- */
.news-section {
    padding: 6rem 0;
    background: var(--bg-off-white);
}

.news-grid {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap: 2rem;
}

.news-card {
    background: white;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: var(--shadow-soft);
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
}

.news-card:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-hover);
}

.news-card.featured {
    grid-row: span 2;
}

.news-image {
    height: 220px;
    overflow: hidden;
}

.news-card.featured .news-image {
    height: 350px;
}

.news-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.news-card:hover .news-image img {
    transform: scale(1.05);
}

.news-content {
    padding: 1.5rem;
    flex: 1;
    display: flex;
    flex-direction: column;
}

.news-category {
    align-self: flex-start;
    background: rgba(0, 237, 100, 0.1);
    color: var(--primary-dark);
    padding: 0.4rem 1rem;
    border-radius: 50px;
    font-size: 0.8rem;
    font-weight: 600;
    margin-bottom: 1rem;
}

.news-content h3 {
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
    color: var(--primary-dark);
    line-height: 1.4;
}

.news-content p {
    color: var(--text-gray);
    font-size: 0.95rem;
    margin-bottom: auto;
}

.news-time {
    margin-top: 1rem;
    font-size: 0.85rem;
    color: #999;
}

/* --- CTA Section --- */
.cta-section {
    padding: 6rem 0;
    background: var(--primary-dark);
    color: white;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.cta-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at center, rgba(0, 237, 100, 0.05), transparent 70%);
}

.cta-content {
    position: relative;
    z-index: 2;
    max-width: 800px;
    margin: 0 auto;
}

.cta-content h2 {
    font-size: 3rem;
    margin-bottom: 1.5rem;
}

.cta-content p {
    font-size: 1.2rem;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 3rem;
}

.cta-buttons {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
}

/* --- Footer --- */
.footer {
    background: #0b1215;
    color: #8a99a8;
    padding: 4rem 0 2rem;
}

.footer-content {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap: 4rem;
    margin-bottom: 3rem;
}

.footer-section h3 {
    color: white;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.footer-section h4 {
    color: white;
    font-size: 1.1rem;
    margin-bottom: 1.5rem;
}

.footer-section ul {
    list-style: none;
    padding: 0;
}

.footer-section ul li {
    margin-bottom: 0.8rem;
}

.footer-section ul li a {
    color: #8a99a8;
    text-decoration: none;
    transition: color 0.3s ease;
}

.footer-section ul li a:hover {
    color: var(--primary-green);
}

.footer-bottom {
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: 2rem;
    text-align: center;
    font-size: 0.9rem;
}

/* --- Responsive --- */
@media (max-width: 1024px) {
    .hero-title {
        font-size: 3rem;
    }

    .news-grid {
        grid-template-columns: 1fr 1fr;
    }

    .news-card.featured {
        grid-row: auto;
        grid-column: span 2;
    }
}

@media (max-width: 768px) {
    .hero-content {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .hero-actions {
        justify-content: center;
    }

    .hero-image {
        margin-top: 3rem;
    }

    .top-nav ul {
        display: none;
    }

    /* Ideally add a mobile menu here */
    .news-grid {
        grid-template-columns: 1fr;
    }

    .news-card.featured {
        grid-column: auto;
    }

    .footer-content {
        grid-template-columns: 1fr;
        text-align: center;
    }
}