.dashboard-container {
    display: flex;
    min-height: 100vh;
    font-size: 16.5px;
    /* "Little big" font size */
}

/* === Sidebar Navigation === */
.nav-link {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 0.75rem 1.5rem;
    color: #475569;
    /* Slate 600 */
    text-decoration: none;
    border-radius: 8px;
    transition: all 0.2s ease;
    font-size: 0.875rem;
    font-weight: 500;
}

.nav-link:hover {
    background: #ffedd5;
    /* Orange 100 */
    color: #ea580c;
    /* Orange 600 */
}

.nav-link.active {
    background: linear-gradient(135deg, #fb923c 0%, #ea580c 100%);
    /* Orange Gradient */
    color: white;
    box-shadow: 0 4px 12px rgba(234, 88, 12, 0.2);
    border: none;
    backdrop-filter: none;
    font-weight: 600;
}

/* === Sidebar === */
.sidebar {
    min-width: 280px;
    max-width: 380px;
    width: auto;
    /* Light Cream/White Mix Gradient */
    background: linear-gradient(180deg, #ffffff 0%, #fff7ed 100%);
    color: #1e293b;
    /* Dark Slate text */
    display: flex;
    flex-direction: column;
    padding: 1rem 0.25rem;
    /* tighter horizontal for rounded rail */
    transition: width 0.3s ease, transform 0.2s ease;
    flex-shrink: 0;
    position: sticky;
    top: 0;
    height: 100vh;
    border-right: 1px solid #fed7aa;
    /* Soft Orange/Peach border */
    /* Subtle border */
    box-shadow: 0 10px 30px rgba(255, 237, 213, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.5);
    backdrop-filter: saturate(140%) blur(6px);
    -webkit-backdrop-filter: saturate(140%) blur(6px);
    border-top-right-radius: 14px;
    border-bottom-right-radius: 14px;
}

/* allow hiding sidebar with a transform for small screens or when toggled */
.sidebar--hidden {
    transform: translateX(-110%);
}

.sidebar-header {
    padding: 0 1.5rem 1.5rem 1.5rem;
    font-size: 1.5rem;
    font-weight: 600;
    color: #1e293b;
    /* Dark Slate */
    border-bottom: 1px solid #fed7aa;
}

.sidebar-nav {
    list-style: none;
    margin-top: 1.5rem;
    flex-grow: 1;
    flex: 1 1 auto;
    overflow-y: auto;
    overflow-x: hidden;
    min-height: 0;
    max-height: none !important;
    padding-right: 6px;
    /* room for custom scrollbar */
    /* Force scrollbar to be visible when content overflows */
    scrollbar-gutter: stable;
}

/* Specific styles for accountant sidebar to ensure all items are visible */
#accountant-sidebar-nav {
    overflow-y: scroll !important;
    max-height: none !important;
    height: 100% !important;
}

.nav-item a {
    display: flex;
    align-items: center;
    padding: 0.75rem 1rem;
    color: #475569;
    /* improved contrast for better readability */
    text-decoration: none;
    transition: background-color 0.2s, color 0.2s, transform 0.2s ease;
    border-left: 3px solid transparent;
    margin: 2px 6px;
    border-radius: 10px;
}

.nav-item a:hover {
    background: rgba(56, 189, 248, 0.1);
    color: #38bdf8;
    transform: translateX(4px);
}

.nav-item a.active {
    background: linear-gradient(90deg, #0ea5e9 0%, #0284c7 100%);
    color: #ffffff;
    font-weight: 600;
    box-shadow: 0 4px 12px rgba(14, 165, 233, 0.4);
    border-left: none;
}

.nav-icon {
    margin-right: 1rem;
    width: 20px;
    /* Aligns icons neatly */
}

/* === Sidebar alignment variants === */
/* For Staff layout: hug the left edge flush */
.sidebar--flush-left {
    padding-left: 0 !important;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.sidebar--flush-left .sidebar-nav {
    padding-right: 6px;
}

.sidebar--flush-left .sidebar-header {
    padding-left: 1rem;
}

/* Custom thin scrollbar for sidebar nav */
.sidebar-nav::-webkit-scrollbar {
    width: 10px;
}

.sidebar-nav::-webkit-scrollbar-track {
    background: rgba(15, 23, 42, 0.3);
    border-radius: 10px;
}

.sidebar-nav::-webkit-scrollbar-thumb {
    background: rgba(148, 163, 184, 0.6);
    border-radius: 10px;
    border: 2px solid rgba(15, 23, 42, 0.3);
}

.sidebar-nav::-webkit-scrollbar-thumb:hover {
    background: rgba(148, 163, 184, 0.8);
}

.sidebar-nav {
    scrollbar-width: thin;
    scrollbar-color: rgba(148, 163, 184, 0.6) rgba(15, 23, 42, 0.3);
}

/* === Main Content === */
.main-content-wrapper {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.dashboard-header {
    background: linear-gradient(180deg, #ffffff 0%, #fff7ed 100%);
    /* Light Cream/White Mix */
    padding: 10px 2rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: auto;
    min-height: 72px;
    position: sticky;
    top: 0;
    z-index: 100;
    border-bottom: 1px solid #fed7aa;
    box-shadow: 0 4px 6px -1px rgba(255, 237, 213, 0.4);
}

/* Header actions: Profile + Logout */
.user-header-actions {
    display: flex;
    align-items: center;
    gap: 12px;
}

.profile-link {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    color: #1e293b;
    /* Dark Slate text */
    text-decoration: none;
    font-weight: 500;
    padding: 8px 10px;
    border-radius: 6px;
    background: transparent;
    border: none;
    cursor: pointer;
}

.profile-link:hover {
    background: var(--surface-alt);
}

.profile-link i {
    font-size: 1rem;
}

/* Profile dropdown */
.profile-menu {
    position: relative;
}

.profile-dropdown {
    position: absolute;
    right: 0;
    top: calc(100% + 6px);
    min-width: 180px;
    background: var(--surface-card);
    border: 1px solid var(--border-subtle);
    border-radius: 8px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.08);
    padding: 6px;
    z-index: 20;
}

.dropdown-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 12px;
    border-radius: 6px;
    color: var(--text-default);
    text-decoration: none;
}

.dropdown-item:hover {
    background: var(--surface-alt);
}

.logout-button {
    background: linear-gradient(135deg, var(--role-primary), var(--role-secondary));
    color: var(--text-default);
    border: 1px solid var(--role-primary);
    padding: 10px 20px;
    border-radius: 8px;
    font-family: 'Poppins', sans-serif;
    font-size: 0.95rem;
    font-weight: 600;
    cursor: pointer;
    transition: filter 0.2s ease, box-shadow 0.2s ease;
    box-shadow: 0 6px 14px color-mix(in oklab, var(--role-primary) 28%, transparent);
    position: relative;
    z-index: 101;
}

.logout-button:hover {
    filter: brightness(1.05);
}

.dashboard-content {
    flex-grow: 1;
    padding: 1.5rem 2rem;
    /* consistent padding */
    background-color: var(--surface-base);
}

/* Content sections: cards and grid utilities */
.dash-grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 1rem;
}

.dash-card {
    background: var(--white-color);
    border: 1px solid #eee;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
    padding: 1rem;
    transition: transform .2s ease, box-shadow .2s ease;
}

.dash-card:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 18px rgba(0, 0, 0, 0.06);
}

.col-3 {
    grid-column: span 3;
}

.col-4 {
    grid-column: span 4;
}

.col-6 {
    grid-column: span 6;
}

.col-12 {
    grid-column: span 12;
}

.stat {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.stat .label {
    color: #6b7280;
    font-weight: 500;
}

.stat .value {
    font-size: 1.35rem;
    font-weight: 700;
    color: #111827;
}

.stat .trend {
    font-size: .85rem;
    color: #059669;
}

@media (max-width: 1024px) {

    .col-3,
    .col-4 {
        grid-column: span 6;
    }
}

@media (max-width: 640px) {

    .col-3,
    .col-4,
    .col-6 {
        grid-column: span 12;
    }
}

/* === Compact, refined sizing for better density === */
.dashboard-container {
    font-size: 15px;
    /* improved base font size for better readability */
}

.sidebar {
    min-width: 240px;
    /* widened per request */
    max-width: 320px;
}

.sidebar-header {
    font-size: 1.2rem;
    /* improved header font size for better visibility */
}

.nav-item a {
    padding: 0.65rem 1rem;
    /* improved padding for better touch targets */
    font-size: 1rem;
    /* improved readability for navigation text */
    font-weight: 500;
    /* slightly bolder for better readability */
}

.sidebar .nav-item i,
.nav-icon {
    font-size: 1rem;
    /* increased from 0.65rem for better visibility */
    width: 16px;
    /* increased from 10px */
    height: auto;
    margin-right: 0.75rem;
    /* increased spacing */
    line-height: 1;
    /* avoid extra height */
    display: inline-flex;
    /* better vertical alignment */
    align-items: center;
    justify-content: center;
    background: none !important;
    /* remove any circular bg from globals */
    border-radius: 0 !important;
    /* ensure no round background */
    padding: 0 !important;
    /* remove extra padding */
}

/* Specific rule for attendance verify icon */
.sidebar .nav-item a[href="/manager/attendance"] i {
    font-size: 1.2rem !important;
    width: 20px !important;
    margin-right: 0.8rem !important;
    color: inherit;
    /* inherit color from parent */
}

.dashboard-header {
    height: 64px;
    padding: 0.5rem 1rem;
}

.logout-button {
    padding: 8px 14px;
    font-size: 0.85rem;
    border-radius: 5px;
}

.dashboard-content {
    padding: 1.5rem;
    /* modest padding while allowing more space */
    font-size: 1rem;
    /* improved content font size for better readability */
    line-height: 1.5;
}

/* Tablet tweaks */
@media (max-width: 1024px) {
    .sidebar {
        min-width: 220px;
        max-width: 280px;
    }
}

/* Mobile tweaks */
@media (max-width: 768px) {
    .sidebar {
        min-width: 200px;
        max-width: 260px;
    }

    .sidebar-header {
        font-size: 1rem;
    }

    .nav-item a {
        padding: 0.5rem 0.75rem;
        font-size: 0.85rem;
    }

    .dashboard-header {
        height: auto;
        min-height: 60px;
        padding: 10px 1rem;
    }

    .logout-button {
        padding: 6px 12px;
        font-size: 0.8rem;
    }

    .dashboard-content {
        padding: 1rem;
        font-size: 0.9rem;
    }
}

/* Small mobile tweaks */
@media (max-width: 480px) {
    .sidebar {
        min-width: 180px;
        max-width: 220px;
    }

    .sidebar-header {
        font-size: 0.9rem;
    }

    .nav-item a {
        padding: 0.4rem 0.6rem;
        font-size: 0.8rem;
    }

    .nav-icon {
        width: 12px;
        margin-right: 0.5rem;
    }
}

/* === Readability & Typography Enhancements === */
.dashboard-content h1,
.dashboard-content h2,
.dashboard-content h3 {
    color: #111827;
    /* very dark gray for strong contrast */
    line-height: 1.25;
    letter-spacing: 0.2px;
    margin: 0 0 0.5rem 0;
}

.dashboard-content p {
    color: #1f2937;
    /* dark slate */
    line-height: 1.6;
}

.dash-card .card-title {
    font-size: 1.05rem;
    font-weight: 600;
    color: #0f172a;
    margin-bottom: 0.5rem;
}

/* Welcome area */
.welcome-title {
    font-size: 1.5rem;
    font-weight: 700;
    color: #0f172a;
    /* near-black */
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
}

.welcome-subtitle {
    font-size: 1rem;
    color: #334155;
    /* slate */
}

/* Utility helpers for contrast */
.high-contrast {
    color: #0b1324 !important;
}

.text-shadow-sm {
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.12);
}

.muted {
    color: #6b7280;
}

/* Lists inside content */
.content-list {
    margin: 0.25rem 0 1rem 1.25rem;
}

.content-list li {
    margin: 0.25rem 0;
}

/* Progress badge for percentages */
.badge-progress {
    display: inline-block;
    background: var(--primary-color);
    color: var(--white-color);
    border-radius: 999px;
    padding: 2px 10px;
    font-size: 0.75rem;
    font-weight: 600;
}

/* === Top navigation (moved from sidebar) === */
.top-nav {
    display: flex;
    gap: 14px;
    align-items: center;
    flex-wrap: wrap;
    max-width: 100%;
    overflow-x: auto;
    scrollbar-width: thin;
}

.top-nav a {
    color: #475569;
    text-decoration: none;
    padding: 8px 12px;
    border-radius: 999px;
    transition: background-color .2s ease, color .2s ease;
    white-space: nowrap;
}

.top-nav a:hover {
    background: rgba(99, 102, 241, 0.12);
    color: #1f2937;
}

.top-nav a.active {
    background: #4f46e5;
    color: #ffffff;
    font-weight: 600;
}

/* Sidebar logout button spacing */
.sidebar .btn {
    margin: 0 1rem;
}

@media (max-width: 768px) {
    .top-nav {
        gap: 8px;
    }

    .top-nav a {
        padding: 4px 8px;
        font-size: 0.875rem;
    }
}

/* === Profile Section Styles === */
.user-avatar-small {
    width: 36px;
    height: 36px;
    background-color: #e0f2fe;
    color: #0284c7;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
}

.user-info-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    line-height: 1.2;
}

.user-name {
    font-size: 14px;
    font-weight: 600;
    color: #0f172a;
}

.user-role {
    font-size: 12px;
    color: #64748b;
    text-transform: capitalize;
}

.dropdown-user-details {
    padding: 12px;
    display: flex;
    flex-direction: column;
}

.dropdown-user-details strong {
    color: #0f172a;
    font-size: 14px;
}

.dropdown-user-details span {
    color: #64748b;
    font-size: 12px;
}

.dropdown-divider {
    border: none;
    border-top: 1px solid #e2e8f0;
    margin: 4px 0;
}