/* AccountantDashboard.css */
.accountant-dashboard {
  padding: 24px;
  background-color: #f8fafc;
  min-height: 100vh;
  font-family: 'Inter', sans-serif;
  overflow-x: hidden;
  /* Prevent horizontal scroll causing issues */
}

/* Header Section */
.dashboard-header {
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  color: white;
  padding: 32px;
  border-radius: 16px;
  margin-bottom: 32px;
  box-shadow: 0 10px 25px -5px rgba(15, 23, 42, 0.3);
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  /* Allow wrapping on small screens */
  gap: 16px;
}

.dashboard-header::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 40%;
  background: linear-gradient(to left, rgba(14, 165, 233, 0.2), transparent);
  pointer-events: none;
}

.header-content {
  z-index: 1;
  max-width: 100%;
}

.header-content h1 {
  font-size: 28px;
  font-weight: 700;
  margin: 0 0 8px 0;
  color: #ffffff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header-content p {
  color: #94a3b8;
  margin: 0;
  font-size: 14px;
}

.refresh-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  cursor: pointer;
  transition: all 0.2s ease;
  z-index: 10;
  white-space: nowrap;
  /* Prevent button text wrap */
}

.refresh-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-1px);
}

.spin-icon {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* KPI Cards Grid */
.kpi-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  /* Increased min-width to prevent squashing */
  gap: 24px;
  margin-bottom: 32px;
}

.kpi-card {
  background: white;
  padding: 24px;
  border-radius: 16px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
  border: 1px solid #e2e8f0;
  transition: transform 0.2s, box-shadow 0.2s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 160px;
  /* Ensure consistent height */
}

.kpi-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 20px -5px rgba(0, 0, 0, 0.1);
}

.kpi-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 16px;
  gap: 12px;
}

.icon-wrapper {
  padding: 12px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  /* Prevent icon shrinking */
}

.growth-badge {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 12px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 20px;
  white-space: nowrap;
  /* Prevent badge text wrap */
  max-width: 100%;
}

.growth-positive {
  color: #16a34a;
  background: #dcfce7;
}

.growth-negative {
  color: #dc2626;
  background: #fee2e2;
}

.kpi-title {
  color: #64748b;
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.kpi-value {
  font-size: 24px;
  /* Slightly reduced from 28px to prevent overflow */
  font-weight: 800;
  color: #0f172a;
  margin: 0;
  letter-spacing: -0.5px;
  word-break: break-all;
  /* Ensure long numbers break if necessary */
  overflow-wrap: break-word;
}

.kpi-subtitle {
  font-size: 12px;
  color: #94a3b8;
  margin-top: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Financial Modules Grid */
.section-title {
  font-size: 18px;
  font-weight: 700;
  color: #0f172a;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.modules-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 24px;
}

.module-card {
  display: flex;
  align-items: center;
  gap: 20px;
  background: white;
  padding: 24px;
  border-radius: 16px;
  border: 1px solid #e2e8f0;
  text-decoration: none;
  transition: all 0.2s ease;
  min-height: 100px;
}

.module-card:hover {
  border-color: #38bdf8;
  box-shadow: 0 4px 15px rgba(56, 189, 248, 0.15);
  transform: translateY(-2px);
}

.module-icon {
  width: 56px;
  height: 56px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  transition: all 0.2s;
  flex-shrink: 0;
}

.module-card:hover .module-icon {
  background-color: #0ea5e9;
  color: white !important;
}

.module-info {
  flex: 1;
  min-width: 0;
  /* Enable truncation for flex children */
}

.module-info h3 {
  margin: 0 0 4px 0;
  color: #334155;
  font-size: 16px;
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.module-info p {
  margin: 0;
  color: #94a3b8;
  font-size: 13px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Colors for specific cards */
.blue-theme {
  color: #2563eb;
  background: #eff6ff;
}

.green-theme {
  color: #16a34a;
  background: #f0fdf4;
}

.purple-theme {
  color: #9333ea;
  background: #faf5ff;
}

.orange-theme {
  color: #ea580c;
  background: #fff7ed;
}

.teal-theme {
  color: #0d9488;
  background: #f0fdfa;
}

/* Loading State */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
}

.loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid #e2e8f0;
  border-top-color: #0ea5e9;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}